.SimpleCaptureImageList {
    display: flex;
    overflow: auto;
}
  
.SimpleCaptureImageList::-webkit-scrollbar {
    display: none;
}

.MainSceneRoot {
    width: 100%;
    background-color: white;
}

.MainSceneRoot::-webkit-scrollbar {
    display: none;
}

.CameraCaptureEffect {
    position: fixed;
    height: 100%;
    width: 100%;
    left: 0%;
    top: 0%;
    background : white;
    opacity: 1;
    display: none;
}

.CameraCaptureAnimation {
    display: block;
    animation: CameraCaptureAnimationKeyframe 1s linear;
}

/*
blink 1s linear infinite alternate
*/
@keyframes CameraCaptureAnimationKeyframe {
    from {
      opacity: 1;
      display: block;
    }
    to {
      opacity: 0;
      display: none;
    }
  }
   